// This is the JSON way to define React Router rules in a Rekit app.
// Learn more from: http://rekit.js.org/docs/routing.html

import {
  Layout,
  TransactionsList,
  Transaction,
  NewTransaction
} from './';
import { PageNotFound } from '../common';

export default {
  path: 'transactions',
  component: Layout,
  childRoutes: [
    { path: "", component: TransactionsList, name: "", icon: "fa fa-credit-card" },
    { path: "update/:id", component: Transaction, name: " ", icon: "fa fa-credit-card", inner: true },
    { path: "add", component: NewTransaction, name: " ", icon: "fa fa-credit-card", inner: true },
    { path: '*', name: 'Page not found', component: PageNotFound, inner: true },
  ],
};
